div.movieImg{
    text-align: center;
}
div.proceedToLogin{
    text-align: center;
}
button.proceedToLogin {
    height: 5vw;
    width: 20vw;
    background-image: linear-gradient(to right top, #ff0000, #d90004, #b50006, #910005, #6f0000);
    border-radius: 43px;
    border: 0;
    font-size: 1.5vw;
    color: white;
}
button.proceedToLogin:hover{
    background-image: linear-gradient(to right top, #410000, #6d000d, #9d0011, #cd000e, #ff0000);
    box-shadow:0 0 40px red;
    text-shadow: 0 0 10px yellow;
}
